import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

import About from '../../components/About';
import Contact from '../../components/Contact';
import Footer from '../../components/Layout/Footer/Footer';
import Lecturers from '../../components/Lecturers/Lecturers';
import HeaderWithDescription from '../../components/HeaderWithDescription/HeaderWithDescription';

import './styles.module.scss';

// ImagePath
import upcommingWibPath from '../../assets/images/upcomming-wib-st.png';
import userOnePath from '../../assets/images/user-1.png';
import userTwoPath from '../../assets/images/auth-1.png';

export default function UpcWebinars() {
  const { t } = useTranslation();

  const navigate = useNavigate()

  const clickBookNowHandled = () => {
     navigate('/signup')
  }

    // From API
    const lecturersItems = [
      {
        id: 1,
        avatar: userOnePath,
        name: "الدكتور رجا المرزوقى",
        description: "نائب رئيس مجلس ادارة جمعية صفا  - استاذ الاقتصاد المشارك معهد الامير سعود الفيصل للدراسات الدبلوماسية"
      },
      {
        id: 2,
        avatar: userTwoPath,
        name: "الدكتور رجا المرزوقى",
        description: "نائب رئيس مجلس ادارة جمعية صفا  - استاذ الاقتصاد المشارك معهد الامير سعود الفيصل للدراسات الدبلوماسية"
      }
    ];

  return (
    <div>
      <HeaderWithDescription
        rate={4.5}
        btnText={t('HeaderWithDescription.book')}
        onClick={clickBookNowHandled}
        src={upcommingWibPath}
        date="2022-1-11 13:23:44"
      />

      <Lecturers lecturersItems={lecturersItems} />

      <About/>
      <Contact/>
      <Footer/>
    </div>
  );
}
