import { t } from "i18next";
import moment from "moment";
import i18n from '../../i18n';

const time = i18n.language === "ar" ? "LTA" : "LT";

export const DefaultMomentFormat = (date: string) => {
  return `${moment(date).format(`dddd  l`)} ${moment(date).format(time)}`
}

export const MomentMonthFromTimeFormat = (date: string) => {
  return `${moment(date).format(`dddd LL`)} ${t('from')} ${moment(date).format(time)}`
}